import React, { useState, useEffect } from 'react';
import Bar from '../components/appbar';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import io from 'socket.io-client';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: '100%',
    padding: theme.spacing(2),
    boxSizing: 'border-box',
  },
  toolbar: theme.mixins.toolbar,
  row: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(2),
    boxSizing: 'border-box',
  },
  text: {
    fontSize: '14px',
    color: theme.palette.text.primary,
    paddingLeft: theme.spacing(1),
  },
  progressList: {
    marginTop: theme.spacing(2),
    maxHeight: '400px',
    overflowY: 'auto',
    border: '1px solid #ccc',
    padding: theme.spacing(1),
    borderRadius: 4,
  },
  progressItem: {
    marginBottom: theme.spacing(1),
  },
}));

const Test = ({ dispatch, history }) => {
  const classes = useStyles();
  const [status, setStatus] = useState('...');
  const [progress, setProgress] = useState([]); // 
  const [socket, setSocket] = useState(null);

  //  Socket.IO 
  useEffect(() => {
    const s = io(); // 
    setSocket(s);

    s.on('progress', data => {
      setProgress(prev => [...prev, data]); // 
    });

    return () => {
      s.disconnect();
    };
  }, []);

  // 
  const handleCheck = async () => {
    setStatus('...');
    setProgress([]); // 
    try {
      const res = await axios.get('/api/trip/checkUrlQueue'); // 
      if (res.data && res.data.message) {
        setStatus('');
      } else {
        setStatus('');
      }
    } catch (err) {
      setStatus('');
    }
  };

  return (
    <div style={{ margin: 0, padding: 0 }}>
      <Bar title={''} history={history} />

      <div className={classes.toolbar} />

      <div className={classes.root}>
        <div className={classes.row}>
          <Button variant="contained" color="primary" onClick={handleCheck}>
            
          </Button>
          <span className={classes.text}>{status}</span>
        </div>

        {/*  */}
        <div className={classes.progressList}>
          {progress.map((item, index) => (
            <div key={index} className={classes.progressItem}>
              {item.tripName} - {item.pointName}: {item.status}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default connect(
  state => state,
  dispatch => ({ dispatch })
)(Test);
